import CardTypeService from '@/services/card/CardTypeService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let CardTypeTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    defaultCardTypeTableFilter: {
      type: String,
      default: null
    },
    defaultCardTypeTableOrderBy: {
      type: String,
      default: null
    },
    typeBehaviourEnumId: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      cardTypeTableData: [],
      cardTypeTableFilterTimer: null,
      cardTypeTableFilter: this.defaultCardTypeTableFilter,
      cardTypeTableOrderBy: this.defaultCardTypeTableOrderBy,
      cardTypeTableContainer: 'loading-container',
      cardTypeTableBusy: false,
      cardTypeTableTotalRows: 0,
      cardTypeTableCurrentPage: 1,
      cardTypeTablePerPage: 10
    }
  },
  computed: {
    cardTypeTableNoDataText: function () {
      return this.cardTypeTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    cardTypeTableTotalPages: function () {
      return Math.ceil(this.cardTypeTableTotalRows / this.cardTypeTablePerPage)
    },
    cardTypeTablePagingInfo: function () {
      return this.pagingInfo(this.cardTypeTableCurrentPage, this.cardTypeTablePerPage, this.cardTypeTableTotalRows)
    }
  },
  methods: {
    loadCardTypeTableData (loadingContainerId) {
      let self = this
      this.cardTypeTableBusy = true
      this.cardTypeTableContainer = loadingContainerId || this.cardTypeTableContainer
      this.startLoading(this.cardTypeTableContainer)
      CardTypeService.cardTypes({
        pageIndex: this.cardTypeTableCurrentPage - 1,
        pageSize: this.cardTypeTablePerPage,
        filter: this.cardTypeTableFilter,
        orderByField: this.cardTypeTableOrderBy,
        typeBehaviourEnumId: this.typeBehaviourEnumId
      }).then(function (response) {
        self.stopLoading(self.cardTypeTableContainer)
        self.cardTypeTableBusy = false
        self.cardTypeTableTotalRows = response.data.totalRows
        self.cardTypeTableData = response.data.cardTypeList || []
      }).catch(function (error) {
        self.stopLoading(self.cardTypeTableContainer)
        self.cardTypeTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshCardTypeTable () {
      this.loadCardTypeTableData()
    },
    filterCardTypeTable (filter) {
      let self = this
      if (this.cardTypeTableFilter !== filter) {
        clearTimeout(this.cardTypeTableFilterTimer)
        this.cardTypeTableFilterTimer = setTimeout(() => {
          self.cardTypeTableFilter = filter
          self.loadCardTypeTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.cardTypeTableFilterTimer)
      }
    },
    orderCardTypeTable (orderBy, active) {
      this.cardTypeTableOrderBy = (active ? '' : '-') + orderBy
      this.loadCardTypeTableData()
    }
  },
  watch: {
    cardTypeTableCurrentPage: function () {
      this.refreshCardTypeTable()
    },
    cardTypeTablePerPage: function () {
      this.refreshCardTypeTable()
    },
    typeBehaviourEnumId: function () {
      this.refreshCardTypeTable()
    }
  }
}

export default CardTypeTableMixin
